<template>
    <div class="sTheme">
        <div class="checkThemeStatus">
            <el-radio v-show="tree.indexOf(radioPower[0]) >-1" v-model="radio" @change="changeRadio" label="1" size="medium" :border="checkBorder">查 询</el-radio>
            <el-radio v-show="tree.indexOf(radioPower[1]) >-1" v-model="radio" @change="changeRadio" label="2" size="medium" :border="checkBorder">修 改</el-radio>
            <el-radio v-show="tree.indexOf(radioPower[2]) >-1" v-model="radio" @change="changeRadio" label="3" size="medium" :border="checkBorder">转 号</el-radio>
        </div>
        <!--<div>{{msg}}</div>-->
        <div class="groupBtn">
            <!--themeBtn-->
            <el-row :gutter="30" class="themeBtnToNext" v-show="isTheme">
                <el-col :xs="12" :sm="8" v-for="(item, index) in themeList" :key="index">
                    <el-button-group>
                        <router-link :to="toNext">
                            <el-button :type="((item.name==curTheme)? 'warning' : 'primary')" @click="themeBtnClick(item.name)">
                                <div class="themeName">
                                    <div class="themeNameTextWarp">
                                        <div class="themeNameText">
                                            <p>{{item.name}}</p>
                                        </div>
                                    </div>
                                </div>
                                <div class="themeWait">{{item.wait}}人</div>
                            </el-button>
                        </router-link>
                    </el-button-group>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>

export default {
  name: 'ServiceTheme',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      radio: '1',
      radioPower: ['a1_b1_c1', 'a1_b1_c2', 'a1_b1_c3'],
      tree: [],

      isTheme: true,
      curTheme: '',
      themeList: [],

      checkBorder: true,
      toNext: '/reference/matter'

    }
  },
  watch: {
    // radio: function(){
    //     this.msg='12';
    // }
  },
  created: function () {
    // if (sessionStorage.initNum == 1) {
    //   sessionStorage.initNum = 2
    //   window.location.reload()
    // }
    if (sessionStorage.reference) {
      let obj = JSON.parse(sessionStorage.reference)
      this.radio = obj.radio
      this.curTheme = obj.curTheme
    } else {}
    if (sessionStorage.userInfo) {
      this.tree = JSON.parse(sessionStorage.userInfo).allPermission
    }
    this.getTheme()
  },
  mounted: function () {
  },
  methods: {
    async getTheme () {
      try {
        let params = {
        }
        let res = await this.$api.unsplash.getSubjects(params)
        if (res.errno === '0') {
          this.themeList = res.data.subjects
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    themeBtnClick: function (themeName) {
      this.curTheme = themeName

      // save
      var obj = {
        radio: this.radio,
        curTheme: themeName,
        curMatter: '',
        selectionElementId: [],
        filteredList: [],
        allID: []
      }
      sessionStorage.reference = JSON.stringify(obj)
    },
    changeRadio: function () {
      this.curTheme = ''
      var obj = {
        radio: this.radio,
        curTheme: '',
        curMatter: '',
        selectionElementId: [],
        filteredList: [],
        allID: []
      }
      sessionStorage.reference = JSON.stringify(obj)
    },
    notifyFn (title, msg, type = 'warning') {
      this.$notify({
        title: title,
        message: msg,
        type: type,
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    @media screen and (min-width: 1200px) {
        .themeBtnToNext .el-button{
            font-size: 24px;
        }
    }
    @media screen and (max-width: 1200px) {
        .themeBtnToNext .el-button{
            font-size: 20px;
        }
    }
    .sTheme{
        width: 100%;
        .checkThemeStatus{
            height: 36px;
        }
        .groupBtn{
            width: 100%;
            margin-top: 40px;
            padding: 0 20px 0 20px;
            position: relative;
            .themeBtnToNext{
                width: 100%;
                .el-col{
                    margin-bottom: 3vh;
                    .el-button-group{
                        height: 10vh;
                        width: 100%;
                        .el-button{
                            height: 10vh;
                            width: 100%;
                            padding: 0;
                            /*font-size:24px;*/
                            .themeName{
                                height:10vh;
                                width:70%;
                                float: left;
                                //text-align: center;
                                .themeNameTextWarp{
                                    width: 100%;
                                    height: 100%;
                                    padding: 4px;
                                    text-align: center;
                                    display: table;
                                    .themeNameText{
                                        display: table-cell;
                                        vertical-align: middle;
                                        p{
                                            word-wrap: break-word !important;
                                            word-break: break-all !important;
                                            white-space: normal !important;
                                        }
                                    }
                                }
                            }
                            .themeWait{
                                height:10vh;
                                width:25%;
                                float: right;
                                padding: 4px;
                                line-height: 10vh;
                                text-align: center;
                            }
                        }
                    }
                }
            }
        }
    }

</style>
<style>

</style>



